#kennzahlen {
    height: auto;
    display: flex;
    justify-content: center;
    flex-direction: column;
    position: relative;
    margin: 20px 0px 0px 0px;
    @include breakpoint(medium) {
        margin: 60px 0px 100px 0px;
        flex-direction: row;
        height: 400px; }
    @include breakpoint(large) {
        margin: 100px 0px 150px 0px; }
    .kennzahl {
        flex-basis: 33.333%;
        position: relative;
        text-align: center;
        padding: 0 10px;
        &:first-child {
            padding-left: 0; }
        &:last-child {
            padding-right: 0; }
        .kennzahl-img {
            max-width: 200px;
            @include breakpoint(medium) {
                max-width: 23vw; }
            @include breakpoint(large) {
                max-width: 250px; }
            //padding: 0 40px
 } }            //transform: scale(0.5)
    .kennzahl-text-wrapper {
        margin-top: 15px;
        display: block;
        position: relative;
        @include breakpoint(medium) {
            margin-top: 30px; } }
    .number {
        display: block;
        color: $primary-color;
        font-size: 45px;
        font-weight: bold;
        padding-bottom: 15px;
        line-height: 1.2em;
        @include breakpoint(medium) {
            padding-bottom: 30px; } }
    p {
 } }        //font-size: 120%



