#primary, #full-width-page-wrapper {
    overflow: hidden; }

.container-animation {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    max-width: 1320px;
    margin-right: auto;
    margin-left: auto; }

.container-animation-small {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    max-width: 1140px;
    margin-right: auto;
    margin-left: auto; }

.col-md .container-animation {
    padding-right: 0;
    padding-left: 0; }


hr {
    display: block;
    width: 100%;
    border: 0px solid transparent;
    height: 3px;
    background-color: black;
    margin: 70px 0px;
    @include breakpoint(medium) {
        margin: 100px 0px; } }

.zwischentext {
    width: 100vw;
    background-color: $primary-color-opacity;
    margin: 50px 0px 20px 0;
    padding: 30px 20px 20px 20px;
    color: $black;
    @include breakpoint(medium) {
        margin: 180px 0px 100px 0;
        padding: 60px 100px 40px 100px; }
    h2 {
        color: $primary-color; } }

.zwischentext.zitat {
    margin-bottom: 0px; }

// Breakpoint Show
.show-desktop {
    display: none;
    @include breakpoint(medium) {
        display: block; } }

.show-mobile {
    display: block;
    @include breakpoint(medium) {
        display: none; } }


// Header Button Fix
.homepage-header-buttons {
    flex-direction: column;
    .btn {
        margin: 10px 0; }
    @include breakpoint(medium) {
        flex-direction: row;
        .btn {
            margin: 0; } } }

.post-image.home-image .cta {
    padding: 1em 0.5em;
    @include breakpoint(medium) {
        padding: 1.5em; } }


.center {
    text-align: center; }
