// Global Styles


//
// Navigation
//

.navbar {
    padding: 0;
    // +breakpoint(large)
 }    //     padding: .5rem 1rem

#wrapper-navbar {
    margin-top: 0rem;
    padding: 0 1rem 0 1rem;
    @include breakpoint(large) {
        margin-top: 1rem; } }

// Velafirca Logo Fix
.unternehmen-velafrica #wrapper-navbar {
    margin-top: 0rem;
    @include breakpoint(large) {
        margin-top: .5rem; } }

.navbar-expand-lg {
    align-items: flex-end;
    .navbar-brand img {
        margin: .5rem 0;
        @include breakpoint(large) {
            margin: 0 0 1.2rem 0; } } }

// Velafrica Logo Fix
.unternehmen-velafrica .navbar-expand-lg .navbar-brand img {
        margin: .5rem 0;
        @include breakpoint(large) {
            margin: 0 0 .5rem 0; } }

.navbar-toggler {
    margin-bottom: 1rem; }

#main-menu {
    .dropdown-menu {
 } }        //top: 96%

.navbar .navbar-nav li {
    @include breakpoint(large) {
        &.small a {
            color: $primary;
            font-size: .8rem;
            padding-top: .6rem;
            padding-bottom: .4rem; }
        padding-left: 0.2rem;
        margin-bottom: 1.6rem;
        a {
            font-size: 0.9rem;
            line-height: 1.8rem; }
        .dropdown-menu li {
            margin-bottom: 0; } }

    @include breakpoint(xlarge) {
        &.small a {
            font-size: .9rem; }
        padding-left: 0.8rem;
        a {
            font-size: 1.1rem; } }

    @include breakpoint(1300px) {
        &.small a {
            font-size: .95rem; }
        padding-left: 1.5rem;
        a {
            font-size: 1.2rem; } } }


// Sub-Navigation

.breadcrumb-container {
    ul.nav {
        flex-direction: column;
        padding: 0;
        margin: 1rem 0 1.2rem 0;
        @include breakpoint(large) {
            flex-direction: row;
            margin-bottom: 0; }
        li {
            &.current_page_item a {
                //box-shadow: 0 0 0 2px $primary
                background-color: $primary;
                color: $white;
                //font-weight: bold
                @include breakpoint(large) {
                    //box-shadow: 0 0 0 0 transparent
                    background-color: transparent;
                    color: $secondary;
                    //&font-weight: bold
 } } }                    //text-decoration: underline
        a {
            @extend .subnav-button-style; } } }

//
// Bereichs Seite
//

.post-image {
    position: relative; }
.opening-time {
    display: none;
    text-align: center;
    background-color: $primary;
    position: absolute;
    color: white;
    width: 20rem;
    height: 20rem;
    border-radius: 10rem;
    padding: 3.5rem 1rem 0 1rem;
    font-size: 1rem;
    line-height: 1.5rem;
    transform: scale(1);
    right: 10%;
    bottom: -15%;
    @include breakpoint(large) {
        display: block; }
    h3 {
        color: white;
        font-size: 1.2rem;
        margin: 0 0 1rem 0;
        //line-height: 1.9rem
        text-transform: uppercase; }
    p {
        margin-bottom: 1rem; } }
.opening-time-mobile {
    background-color: $primary;
    text-align: center;
    color: white;
    margin: 1rem 0;
    padding: 1.5rem 1rem .5rem 1rem;
    font-size: 1rem;
    @include breakpoint(large) {
        display: none; }
    h3 {
        color: white;
        font-size: 1rem;
        margin: 0 0 1rem 0;
        //line-height: 1.9rem
        text-transform: uppercase; }
    p {
        margin-bottom: 1rem; } }




//
// News
//

.news-container {
    h3 {
        font-size: 1.3rem; } }





//
// WooCommerce
//

// Archive
.woocommerce-products-header__title {
    display: none; }

.woocommerce-ordering {
    display: none; }

.shop-breadcrumb {
    font-size: 1.8rem;
    color: $secondary;
    //a
 }    //  //text-decoration: underline

.woocommerce-categories {
    padding: 0 !important;
    margin: 0 0 2rem 0 !important;
    .woocommerce-product-category-page {
        display: block;
        margin: 0 0 0.5rem 0;
        padding: 0;
        // a
        //     padding: .5rem
        //     border: 2px solid $primary
        //     &:hover
        //         color: $secondary
        //         border-color: $secondary
        &::marker {
            content: ""; }
        a {
            display: block;
            text-align: left; }
        @include breakpoint(large) {
            display: inline-block;
            margin: 0 1rem 0.5rem 0;
            a {
                display: inline-block;
                text-align: center; } } }
    .shop-category-button {
 } }        //@extend .subnav-button-style

// Single
.wc-tabs {
    display: none; }

.woocommerce button.button.alt {
    background-color: $primary !important; }

.woocommerce-product-category-page {
    .uncategorized {
        display: none; } }

